@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yaldevi&display=swap);
.App {
  text-align: center;
  background-color: #25234e;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  padding: 0;
  margin: 0;
}
body {
  height: 1000px;
}
.menu {
  width: 45%;
  margin: 0 15% 25px 40%;
  padding-top: 15px;
}
.menu ul {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}
li {
  list-style: none;
  height: 15px;
  text-align: center;
}
.link {
  text-decoration: none;
  color: #838383;
  font-size: 17px;
  font-family: "Bebas Neue", cursive;
}
.link:hover {
  text-decoration: none;
  color: #ced3cc;
  font-size: 18px;
  padding-bottom: 5px;
  border-bottom: solid 2px #0bbd40;
}

.general {
  display: flex;
}
.mainBlock,
.choose-curr,
.instance,
table {
  font-family: "Yaldevi", sans-serif;
  font-size: 10px;
  background: white;
  width: 60%;
  border-collapse: collapse;
  text-align: center;
  margin: 10px 15px 15px 15px;
  border-radius: 5px;
  padding-top: 5px;
}
table {
  width: 100%;
  margin: 0;
}
th {
  color: rgb(4, 68, 31);
  border-bottom: 2px solid #afafaf;
  padding: 10px 8px 30px;
}
td {
  color: black;
  font-weight: bold;
  padding: 5px 8px;
  transition: 0.3s linear;
}
td:nth-child(1) {
  color: #0bbd40;
  font-weight: bold;
  font-size: 12px;
}
td:nth-child(3) {
  color: #a12323;
  font-weight: bold;
  font-size: 12px;
}
td img {
  width: 100%;
  height: 40px;
}
h4 {
  font-size: 30px;
}
.find-block {
  width: 98.5%;
  display: flex;
  font-weight: bold;
  text-align: left;
  padding: 0 2px 10px 10px;
  border-bottom: 2px solid #afafaf62;
  border-radius: 5px;
}
label {
  margin-left: 3px;
  font-size: 12px;
  margin-bottom: 5px;
}
input,
button {
  color: black;
  width: 100%;
  height: 25px;
  border-radius: 8px;
  border: 1px solid rgb(172, 167, 167);
  font-family: "Lucida Sans Unicode", "Lucida Grande", Sans-Serif;
  font-size: 12px;
  padding-left: 5px;
}
input:focus {
  outline: none;
}
button {
  width: 20%;
  background-color: rgb(56, 56, 87);
  color: rgb(216, 212, 212);
  margin: 20px 20px 20px 20px;
  height: 26px;
}
button:hover {
  color: #afafaf;
  font-size: 13px;
  cursor: pointer;
}
.find {
  width: 20%;
  margin: 3px 15px 0 0;
}
.clear {
  font-size: 12px;
  margin-left: 90%;
  font-weight: bold;
  text-decoration: underline;
  color: grey;
}
.clear:hover {
  color: rgb(151, 149, 149);
  cursor: pointer;
}
.right-blocks {
  display: flex;
  flex-flow: column;
  width: 34%;
}
.choose-curr,
.instance {
  width: 97%;
  height: 140px;
  text-align: left;
  padding: 15px 5px 0 25px;
  font-size: 18px;
}
.curren {
  display: flex;
}
.choose-curr label {
  width: 60%;
  font-size: 14px;
  font-weight: bold;
  margin-left: 20px;
  padding: 18px 20px 25px 0;
}
select,
option {
  cursor: pointer;
  width: 32%;
  height: 40px;
  background-color: rgb(56, 56, 87);
  color: rgb(216, 212, 212);
  margin: 20px 10px 20px 0;
  border-radius: 5px;
  text-align: center;
  font-size: 18px;
}
select:hover,
select:focus {
  background-color: rgb(56, 56, 87);
  outline: none;
}
.instan-blocks {
  display: flex;
  justify-content: space-around;
  text-align: center;
  margin: 5px 10px 0 0;
}
.instan-blocks div {
  width: 32%;
}
.active {
  border-right: 1px solid gray;
  border-left: 1px solid gray;
}
.instan-blocks h4 {
  margin-top: 10px;
  font-size: 22px;
}

